<template>
    <div class="back-water">
        <!-- <div class="game-nav">
            <div :class="['nav-item', activeGameType == item.key ? 'active' : '']" v-for="(item, index) in gameBigList" :key="'game-type-' + index" @click="handleSelectGameBigType(item)">
                <span>{{item.title}}</span>
            </div>
        </div> -->
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>回水方案</span>
                    <div class="list-operate">
                        <i-select v-model="selectMethodName" style="width: 185px;" @on-change="handleChangeMethod">
                            <i-option :value="'0'">所有方案</i-option>
                            <i-option v-for="(item, index) in methodNameList" :key="index" :value="item.value">{{item.title}}</i-option>
                        </i-select>
                        <div class="add" @click="handleAddBackwater">添加方案</div>
                    </div>
                </div>
            </div>
            <div class="query-poptip">
                <p>说明：因为玩家回水方案选择中没有区分游戏，选定一种回水方案时，就等于选了所有游戏的方案，所以这里不同的游戏都需要设置一个相同方案名的配置；比如：设置方案名为“方案一”的方案需要添加相同的方案名称6条记录，分别指向不同的6款游戏。</p>
            </div>
            <div class="data-list">
                <div class="data-header">
                    <p class="item-info item-1">方案名称</p>
                    <p class="item-info item-2">适用游戏</p>
                    <p class="item-info item-3">最小统计</p>
                    <p class="item-info item-4">最大统计</p>
                    <p class="item-info item-5">回水比例（%）</p>
                    <p class="item-info item-6">回水模式</p>
                    <p class="item-info item-7">操作</p>
                </div>
                <div class="data-con">
                    <div class="data-item" v-for="(item, index) in backwaterData" :key="index">
                        <p class="item-info item-1">{{item.name}}</p>
                        <p class="item-info item-2">{{gameList.find(ele => {return ele.type == item.type}).name}}</p>
                        <p class="item-info item-3">{{item.minStatistics}}</p>
                        <p class="item-info item-4">{{item.maxStatistics}}</p>
                        <p class="item-info item-5">{{item.proportion}}</p>
                        <p class="item-info item-6">{{modeList.find(ele => {return ele.value == item.mode}).title}}</p>
                        <div class="item-info item-7">
                            <span @click="handleModify(item)">修改</span>
                            <span @click="handleDelete(item)">删除</span>
                        </div>
                    </div>
                </div>
            </div>
            <Page class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        </div>
        <div v-show="backwater.showMask" class="back-water-mask">
            <div v-show="backwater.showMask" class="back-water-item">
                <div class="back-water-item-h">
                    <span>{{backwater.header}}</span>
                    <span class="modal-close" @click="handleCloseModal">X</span>
                </div>
                <div class="back-water-item-c">
                    <div class="item-top">
                        <div class="columns-item">
                            <p  class="columns-item-label">方案名称</p>
                            <i-select v-model="backwater.level" class="columns-item-select">
                                <i-option v-for="(item, index) in methodNameList" :key="index" :value="item.value">{{item.title}}</i-option>
                            </i-select>
                        </div>
                        <div class="columns-item">
                            <p  class="columns-item-label">方案模式</p>
                            <i-select v-model="backwater.mode" class="columns-item-select">
                                <i-option v-for="(item, index) in modeList" :key="index" :value="item.value">{{item.title}}</i-option>
                            </i-select>
                        </div>
                        <div class="columns-item">
                            <p  class="columns-item-label">适用游戏</p>
                            <i-select v-model="backwater.type" class="columns-item-select">
                                <i-option v-for="(item, index) in gameList" :key="index" :value="item.type">{{item.name}}</i-option>
                            </i-select>
                        </div>
                    </div>
                    <div class="item-top">
                        <div class="columns-item">
                            <p  class="columns-item-label">最小统计</p>
                            <InputNumber :min="0" v-model="backwater.minStatistics" class="columns-item-input"></InputNumber>
                        </div>
                        <div class="columns-item">
                            <p  class="columns-item-label">最大统计</p>
                            <InputNumber :min="0" v-model="backwater.maxStatistics" class="columns-item-input"></InputNumber>
                        </div>
                        <div class="columns-item">
                            <p  class="columns-item-label">回水比例</p>
                            <InputNumber :max="100" :min="0" v-model="backwater.proportion" class="columns-item-input"></InputNumber>
                            <span>%</span>
                        </div>
                    </div>
                    <div class="columns-item footer">
                        <div class="submit" @click="handleSubmit">提交</div>
                    </div>
                </div>
            </div>
        </div>
        <Modal v-model="backwater.showDelete" title="确认删除" @on-ok="handleEnterDelete" @on-cancel="backwater.showDelete = false">
            <p>是否确认删除？</p>
        </Modal>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
export default {
    data() {
        let that = this;
        return {
            showLoading: false,
            selectMethodName: '0',
            activeGameType: 'lottery',
            gameBigList: [
                {
                    title: '彩票游戏',
                    key: 'lottery'
                },
                {
                    title: '视讯游戏',
                    key: 'video'
                }
            ],
            backwater: {
                header: '',
                id: '',
                level: '',
                type: '',
                minStatistics: 0,
                maxStatistics: 0,
                proportion: 0,
                mode: '',
                submitText: '',
                showMask: false,
                showDelete: false,
                method: 0
            },
            methodNameList: [
                {
                    title: '方案一',
                    value: '1'
                },
                {
                    title: '方案二',
                    value: '2'
                },
                {
                    title: '方案三',
                    value: '3'
                },
                {
                    title: '方案四',
                    value: '4'
                },
                {
                    title: '方案五',
                    value: '5'
                },
                {
                    title: '方案六',
                    value: '6'
                },
                {
                    title: '方案七',
                    value: '7'
                }
            ],
            modeList: [
                {
                    title: '流水模式',
                    value: '1'
                },
                {
                    title: '输赢模式',
                    value: '2'
                }
            ],
            gameList: [],
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            backwaterData: [],
            isLoading: false
        }
    },
    mounted() {
        let that = this;
        that.getGameList().then(function() {
            that.getData();
        })
    },
    methods: {
        handleSelectGameBigType(item) {
            let that = this;

            that.activeGameType = item.key;
            that.getGameList().then(function() {
                that.getData();
            })
        },
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                if(that.activeGameType == 'lottery') {
                    that.$axios({
                        url: '/api/Merchant/GetGameList'
                    }).then(function(data) {
                        if (data.data.Status === 100) {
                            const result = data.data.Data;

                            that.gameList = result.map(item => {
                                return {
                                    name: item.GameName,
                                    type: item.GameType,
                                    key: item.NickName
                                }
                            }).sort((a, b) => a.type - b.type);
                            that.gameList.unshift({
                                name: '所有游戏',
                                type: '0',
                                key: 'all'
                            })
                            resolve();
                        }
                        reject();
                    })
                } else {
                    that.gameList = [];
                    that.gameList.push({
                        name: '所有游戏',
                        type: '0',
                        key: 'all'
                    });
                    that.gameList.push({
                        name: '百家乐',
                        type: '1',
                        key: 'bjl'
                    });
                    resolve();
                }
            })
        },
        handleChangeMethod() {
            let that = this;

            that.page.pageNum = 1;
            that.getData();
        },
        handleAddBackwater() {
            let that = this;
            that.backwater.header = '添加方案';
            that.backwater.method = 1;
            that.backwater.showMask = true;
        },
        handleModify(item) {
            let that = this;
            that.backwater.header = '修改方案';
            that.backwater.method = 2;
            that.backwater.showMask = true;
            that.getItem(item.id);
        },
        handleDelete(item) {
            let that = this;

            that.backwater.id = item.id;
            that.backwater.showDelete = true;
        },
        handleEnterDelete() {
            let that = this,
                _url = '';

            if (!that.isLoading) {
                that.isLoading = true;
                _url = that.activeGameType == 'lottery' ? '/api/BackwaterSetup/DeleteBackwaterByID' : '/api/BackwaterSetup/DeleteVideoBackwaterByID';
                that.$axios({
                    url: _url,
                    method: 'post',
                    params: {
                        'backID': that.backwater.id
                    }
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.backwater.showDelete = false;
                    if (that.page.total % that.page.pageSize === 1) {
                        that.page.pageNum = parseInt(that.page.total / that.page.pageSize);
                    }
                    that.getData();
                })
            }
        },
        handleCloseModal() {
            let that = this;

            that.backwater = {
                header: '',
                id: '',
                level: '',
                type: '',
                minStatistics: 0,
                maxStatistics: 0,
                proportion: 0,
                mode: '',
                submitText: '',
                showMask: false,
                showDelete: false
            };
            that.backwater.showMask = false;
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.getData();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.getData();
        },
        getData() {
            let that = this,
                param = {},
                _url = '';

            that.page.pageNum == 0 && (that.page.pageNum = 1);
            param['start'] = that.page.pageNum;
            param['pageSize'] = that.page.pageSize;
            param['name'] = that.selectMethodName != '0' ? that.methodNameList.find(item => item.value == that.selectMethodName).title : '';
            _url = that.activeGameType == 'lottery' ? '/api/BackwaterSetup/GetBackwaterList' : '/api/BackwaterSetup/GetVideoBackwaterList';
            if (!that.isLoading) {
                that.isLoading = true;
                that.showLoading = true;

                that.$axios({
                    url: _url,
                    params: param
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.page.total = data.data.Total;

                        that.backwaterData = result.map(item => {
                            return {
                                id: item.ID,
                                level: item.UserType,
                                type: item.GameType || '0',
                                minStatistics: item.Minrecord,
                                maxStatistics: item.Maxrecord,
                                proportion: item.Odds + '%',
                                mode: item.Pattern,
                                name: item.Name
                            }
                        })
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                    that.isLoading = false;
                    that.showLoading = false;
                })
            }
        },
        handleSubmit() {
            let that = this,
                vData = {},
                _url = '';

            if (!that.backwater.maxStatistics) {
                that.$Message.error('请输入最大统计');
                return;
            }

            if (!that.backwater.minStatistics) {
                that.$Message.error('请输入最小统计');
                return;
            }

            if (!that.backwater.proportion) {
                that.$Message.error('请输入返水比例');
                return;
            }

            if (that.backwater.method === 1) {
                if (!that.backwater.mode) {
                    that.$Message.error('请选择返水模式');
                    return;
                }

                if (!that.backwater.level) {
                    that.$Message.error('请选择方案名称');
                    return;
                }

                if (!that.isLoading) {
                    that.isLoading = true;

                    if (that.backwater.type != '0') {
                        vData['GameType'] = that.backwater.type;
                    }
                    vData['Maxrecord'] = that.backwater.maxStatistics;
                    vData['Minrecord'] = that.backwater.minStatistics;
                    vData['Odds'] = that.backwater.proportion;
                    vData['Pattern'] = that.backwater.mode;
                    vData['Name'] = that.methodNameList.find(item => item.value == that.backwater.level).title;
                    _url = that.activeGameType == 'lottery' ? '/api/BackwaterSetup/AddBackwater' : '/api/BackwaterSetup/AddVideoBackwater';
                    that.$axios({
                        url: _url,
                        method: 'post',
                        data: vData
                    }).then(function(data) {
                        if (data.data.Status === 100) {
                            that.$Message.success(data.data.Message);
                        } else {
                            that.$Message.error(data.data.Message);
                        }

                        that.backwater.maxStatistics = 0;
                        that.backwater.minStatistics = 0;
                        that.backwater.proportion = 0;
                        that.backwater.type = '';
                        that.backwater.mode = '';
                        that.backwater.level = '';
                        that.isLoading = false;
                        that.backwater.showMask = false;
                        that.getData();
                    })
                }
            } else {
                if (!that.isLoading) {
                    that.isLoading = true;

                    if (that.backwater.type != '0') {
                        vData['GameType'] = that.backwater.type;
                    }
                    vData['Maxrecord'] = that.backwater.maxStatistics;
                    vData['Minrecord'] = that.backwater.minStatistics;
                    vData['Odds'] = that.backwater.proportion;
                    vData['Pattern'] = that.backwater.mode;
                    vData['Name'] = that.methodNameList.find(item => item.value == that.backwater.level).title;
                    vData['ID'] = that.backwater.id;
                    _url = that.activeGameType == 'lottery' ? '/api/BackwaterSetup/UpdateBackwater' : '/api/BackwaterSetup/UpdateVideoBackwater';
                    that.$axios({
                        url: _url,
                        method: 'post',
                        data: vData
                    }).then(function(data) {
                        if (data.data.Status === 100) {
                            that.$Message.success(data.data.Message);
                        } else {
                            that.$Message.error(data.data.Message);
                        }
                        that.isLoading = false;
                        that.backwater.showMask = false;
                        that.getData();
                    })
                }
            }
        },
        getItem(id) {
            let that = this,
                _url = '';

            _url = that.activeGameType == 'lottery' ? '/api/BackwaterSetup/GetBackwaterByID' : '/api/BackwaterSetup/GetVideoBackwaterByID';
            that.$axios({
                url: _url,
                params: {
                    'backID': id
                }
            }).then(function(data) {
                if (data.data.Status === 100) {
                    const result = data.data.Model;

                    that.backwater.type = (result.GameType || '0').toString(); // 游戏类型 1：赛车 2：飞艇 4：极速 3：时时彩 5：澳10  6：澳5
                    that.backwater.maxStatistics = result.Maxrecord; // 最大统计
                    that.backwater.minStatistics = result.Minrecord; // 最小统计
                    that.backwater.proportion = result.Odds; // 返水比例
                    that.backwater.mode = result.Pattern.toString(); // 模式  1：流水模式 2：输赢模式
                    that.backwater.level = that.methodNameList.find(ele => ele.title == result.Name).value; // 用户类型 1：黄铜  2：白银  3：黄金  4：铂金  5：钻石  6：银皇冠  7：金皇冠
                    that.backwater.id = result.ID;
                }
            })
        }
    },
}
</script>

<style lang="scss" scoped>
.back-water {
    position: relative;
    .game-nav {
        display: flex;
        align-items: stretch;
        margin: 10px 0;
        height: 36px;
        background-color: #f2f2f2;
        border-radius: 4px;
        overflow: hidden;

        .nav-item {
            padding: 0 20px;

            line-height: 36px;

            cursor: pointer;
        }

        .active {
            background-color: rgb(65, 199, 219);
        }
    }
    .content {
        flex-direction: column;

        .list-header {
            width: 100%;

            .list-title {
                justify-content: space-between;

                width: 100%;

                .list-operate {
                    display: flex;
                    align-items: center;

                    font-weight: 400;

                    .add {
                        margin-left: 15px;

                        width: 109px;
                        height: 28px;
                        line-height: 24px;

                        // background-color: rgb(201, 201, 201);
                        // border-radius: 4px;
                        // border: solid 1px rgb(161, 161, 161);
                        background-color: #148f5f;
                        box-shadow: inset 0px 0px 5px 0px rgba(124, 255, 173, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e9051;
                        text-align: center;
                        cursor: pointer;
                        color: #fff;
                    }
                }
            }
        }

        .query-poptip {
            margin-top: 10px;
            padding-left: 6px;

            width: 100%;

            background-color: rgb(228, 228, 228);
            border: 1px solid rgb(215, 215, 215);
            box-sizing: border-box;
            color: rgb(255, 102, 0);
            text-align: left;
        }

        .data-list {
            margin-top: 15px;

            width: 100%;

            border: solid 1px #d7d7d7;

            .data-header, .data-item {
                display: flex;
                align-items: stretch;

                height: 40px;

                background-color: rgb(228, 228, 228);

                .item-info {
                    width: 110px;
                    line-height: 40px;

                    border-right: solid 1px #c9c9c9;
                }

                .item-7 {
                    width: 260px;

                    border-right: 0;

                    & > span {
                        display: inline-block;

                        width: 97px;
                        height: 28px;
                        line-height: 28px;

                        border-radius: 4px;
                        border: solid 1px rgb(0, 102, 102);
                        background-color: rgb(0, 204, 204);
                        cursor: pointer;
                    }

                    & > span:last-child {
                        margin-left: 14px;

                        border-color: rgb(255, 51, 102);
                        background-color: rgb(255, 153, 153);
                    }
                }
            }

            .data-con {
                min-height: 440px;

                overflow: auto;
                .data-item {
                    background-color: #fff;
                }

                .data-item:nth-child(2n+2) {
                    background-color: rgb(249, 249, 249);
                }
            }
        }
    }

    .back-water-mask {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        width: 100%;
        height: 100%;

        background-color: rgba(55,55,55,.6);

        z-index: 1000;

        .back-water-item {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            margin: auto;

            width: 329px;
            height: 493px;

            border: 2px solid rgb(204, 204, 204);

            background-color: rgb(242, 242, 242);

            z-index: 1001;

            .back-water-item-h {
                display: flex;
                justify-content: space-between;

                padding: 5px;

                background-color: #ddd;
                border-bottom: 1px solid rgb(204, 204, 204);

                .modal-close {
                    cursor: pointer;
                }
            }

            .back-water-item-c {
                margin: 23px;

                .item-top {
                    margin-top: 10px;

                    background-color: rgb(242, 242, 242);
                    border: 1px solid rgb(228, 228, 228);
                }

                .item-top:first-child {
                    margin-top: 0;
                }

                .columns-item {
                    display: flex;

                    margin-top: 17px;

                    .columns-item-label {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        flex-shrink: 0;

                        width: 90px;
                    }

                    .columns-item-select, .columns-item-input {
                        margin-right: 10px;

                        width: 260px;

                        text-align: left;
                    }

                    & > span:last-child {
                        display: inline-block;

                        margin-right: 10px;

                        width: 20px;
                        height: 32px;
                        line-height: 32px;
                    }

                    .submit {
                        width: 280px;
                        height: 38px;
                        line-height: 38px;

                        background-color: rgb(102, 153, 204);
                        border-radius: 4px;
                        border: solid 1px rgb(102, 102, 204);
                        text-align: center;
                        cursor: pointer;
                    }
                }

                .columns-item:last-child {
                    margin-bottom: 17px;
                }

                .footer {
                    margin-top: 28px;
                }
            }
        }
    }
}
</style>